import './styles.css';
import './css/normalize.css'



//time variables
const time1 = document.querySelector(".js-1 .js-time");
const time2 = document.querySelector(".js-2 .js-time");
const time3 = document.querySelector(".js-3 .js-time");
const time4 = document.querySelector(".js-4 .js-time");
const time5 = document.querySelector(".js-5 .js-time");

//value variables
const gain1 = document.querySelector(".js-1 .js-gain");
const gain2 = document.querySelector(".js-2 .js-gain");
const gain3 = document.querySelector(".js-3 .js-gain");
const gain4 = document.querySelector(".js-4 .js-gain");


//result variables
const result1 = document.querySelector(".js-1 .result");
const result2 = document.querySelector(".js-2 .result");
const result3 = document.querySelector(".js-3 .result");
const result4 = document.querySelector(".js-4 .result");
const averageResult = document.querySelector('.js-averageResult')


//button variable
const button = document.querySelector("#button");

//calculator function
const expectedGain=(start, stop, gain)=> {
    return gain/(stop-start)*86400000;
}

//clear results
const clearResult = () => {
    result1.innerText='';
    result2.innerText='';
    result3.innerText='';
    result4.innerText='';
    averageResult.innerText='';
}

//on-click
const onButtonClick = () => {
    clearResult();
   const date1 = new Date(time1.value);
   const date2 = new Date(time2.value);
   const date3 = new Date(time3.value);
   const date4 = new Date(time4.value);
   const date5 = new Date(time5.value);
   result1.insertAdjacentText('beforeend', expectedGain(date2, date1, gain1.value));
   result2.insertAdjacentText('beforeend', expectedGain(date3, date2, gain2.value));
   result3.insertAdjacentText('beforeend', expectedGain(date4, date3, gain3.value));
   result4.insertAdjacentText('beforeend', expectedGain(date5, date4, gain4.value));
   averageResult.insertAdjacentText('beforeend', `Средний ожидаемый зароботок в день: ${(expectedGain(date2, date1, gain1.value)+expectedGain(date3, date2, gain2.value)+expectedGain(date4, date3, gain3.value)+expectedGain(date5, date4, gain4.value))/4}`);

  };

button.addEventListener("click", onButtonClick);

